import Header from "./Header";
import ScrollContainer from 'react-indiana-drag-scroll'
import './App.css';
import { useEffect, useRef, useState } from 'react';
import { useLocation, useParams, useNavigate } from 'react-router-dom';
import visibleGallery from './gallery.json';

let data = [];
for (let i = 0; i < 100; i++) {
  const item = visibleGallery.find(({ number }) => number === i);

  if (item) {
    data.push(item)
  } else {
    data.push({
      number: i,
      notMintedYet: true,
      image: i % 2 ? '/gallery/placeholder-curves.png' : '/gallery/placeholder-void.png'
    });
  }
}

const total = data.length;

function App() {

  const stateRef = useRef();
  const params = useParams();
  const location = useLocation();
  const navigate = useNavigate();
  const [visible, setAnimationVisible] = useState(false);

  stateRef.current = params.item !== undefined ? Number(params.item): undefined;

  useEffect(() => {
    const handler = function(e) {
        if (stateRef.current === undefined) {
          return;
        }

        let val;
        switch (e.keyCode) {
            case 27:
              navigate(-1);
              break;
            case 37:
                val = (total + stateRef.current - 1) % total;
                navigate(`/${params.collection}/${val}`, { replace: true });
                break;
            case 39:
                val = (stateRef.current + 1) % total;
                navigate(`/${params.collection}/${val}`, { replace: true });
                break;
            default:
            break;
        }
    };

    document.addEventListener('keydown', handler);

    return () => {
      // unsubscribe event
      // TODO: might not work
      document.removeEventListener("keydown", handler);
    };
  });

  useEffect(() => {
    setAnimationVisible(false);
  }, [params]);

  return (
    <main className="aya-gallery-content">
      <Header />

      <div className="aya-gallery-centered">
         <ScrollContainer className="scroll-container aya-gallery-padding" activationDistance={2} hideScrollbars={false}>
          <section className="aya-gallery-items">
            { data.map((value, index) => (
              <div className="aya-gallery-item" key={index} onClick={ (event) => {
                  navigate(`/${params.collection}/${index}`);
              } }>
                <div className="aya-gallery-item-picture-container">
                  <img
                    className="aya-gallery-item-picture"
                    src={ value.image }
                    width="120"
                    height="400"
                    loading="lazy"
                    alt={`#${ value.number } from Ayahuaska collection` }
                  />
                  <p className="aya-gallery-item-number">{ value.number }</p>
                </div>
              </div>
            ))}
          </section>
        </ScrollContainer>

        <div className="aya-gallery-info">
          <p className="aya-gallery-title">COLLECTION</p>
          <h2 className="aya-gallery-header">Ayahuasca</h2>
        </div>
      </div>

      {
        stateRef.current !== undefined &&
        <div className="aya-gallery-single">
          <div className="aya-gallery-single-content">
            <div className="aya-gallery-item-picture-container-copy"
              /* onMouseEnter={ () => !data[stateRef.current].notMintedYet && setAnimationVisible(false) } */
              /* onMouseLeave={ () => !data[stateRef.current].notMintedYet && setAnimationVisible(false) } */
            >
              { !visible && <img
                className="aya-gallery-item-picture"
                src={data[stateRef.current].image}
                width="400"
                height="400"
                loading="lazy"
                alt={`#${ stateRef.current } from Ayahuaska collection` }
              />
            }

              { visible && <iframe
                className="aya-gallery-item-picture"
                src={ process.env.PUBLIC_URL + `/generate.html?seed=` + data[stateRef.current].hash }
                width="544"
                height="544"
              /> }
            </div>

            { data[stateRef.current].notMintedYet && (
              <div className="aya-gallery-not-minted-yet--mobile">
                <p>This item hasn't been minted yet!</p>

                <a className="aya-gallery-join-discord" href="https://discord.gg/MYhVMFPvxu" rel="noreferrer noopener">
                  Join us in Discord
                </a>
              </div>
            )}

            <div className="aya-gallery-controllers aya-gallery-controllers--mobile">
              <a href="#" onClick={ (e) => {
                  e.preventDefault();
                  const val = (total + stateRef.current - 1) % total;
                  navigate(`/${params.collection}/${val}`, { replace: true });
              }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-left"><polyline points="15 18 9 12 15 6"></polyline></svg>
                Previous
              </a>
              <a href="#" onClick={ (e) => {
                e.preventDefault();
                const val = (stateRef.current + 1) % total;
                navigate(`/${params.collection}/${val}`, { replace: true });
              }}>
                Next
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-right"><polyline points="9 18 15 12 9 6"></polyline></svg>
              </a>
            </div>

            <div className="aya-gallery-single-info">
              <h3 className="aya-gallery-single-collection">Collection: Ayahuasca Art</h3>
              <h1 className="aya-gallery-single-name">Ayahuasca { data[stateRef.current].notMintedYet ? '#???' : '#' + stateRef.current }</h1>

              <p className="aya-gallery-single-description">
                Unique collection of 500 randomly generated artworks inspired by the ancestral Amazon ritual. Each instance explores an abstract composition of curves, motion, and colors living on the Solana blockchain.
              </p>



              { data[stateRef.current].notMintedYet ? (
                <div className="aya-gallery-not-minted-yet">
                  <p>This item hasn't been minted yet.</p>

                  <a className="aya-gallery-join-discord" href="https://discord.gg/MYhVMFPvxu" rel="noreferrer noopener">
                    Join us in Discord
                  </a>
                </div>
              ) : <a
                className="aya-gallery-join-discord"
                href={process.env.PUBLIC_URL + `/generate.html?seed=` + data[stateRef.current].hash}
                target="_blank"
               >
                See generation
              </a>}
            </div>

            <div className="aya-gallery-controllers">
              <a href="#" onClick={ (e) => {
                  e.preventDefault();
                  const val = (total + stateRef.current - 1) % total;
                  navigate(`/${params.collection}/${val}`, { replace: true });
              }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-left"><polyline points="15 18 9 12 15 6"></polyline></svg>
                Previous
              </a>
              <a href="#" onClick={ (e) => {
                e.preventDefault();
                const val = (stateRef.current + 1) % total;
                navigate(`/${params.collection}/${val}`, { replace: true });
              }}>
                Next
                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-right"><polyline points="9 18 15 12 9 6"></polyline></svg>
              </a>
            </div>
          </div>
        </div>
      }
    </main>
  );
}

export default App;
  